.btn {
  white-space: pre;
  min-height: 48px;
  display: grid;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.btn.btn-square {
  width: 48px;
}
.btn.btn-panel {
  padding: 0 1em 0 1em;
}
.btn.btn-rectangle {
  grid-template-columns: 48px auto;
}
.btn:hover {
  color: black;
  cursor: pointer;
}



