.blog {
  display: grid;
  grid-template-areas: "nav center content";
  grid-template-rows: 100vh;
  grid-template-columns: auto auto 65vw;
}
.blog-navigation {
  background-color: white;
  padding: 15vh 0 15vh 0;
  grid-area: nav;
  display: grid;
  grid-template-areas: "latest" "categories";
  grid-template-rows: 35vh 35vh;
  -webkit-transition: width 0.8s ease-in-out;
  -o-transition: width 0.8s ease-in-out;
  transition: width 0.8s ease-in-out;
}
.blog-navigation-latest,
.blog-navigation-categories {
  margin: 0 2em 0 2em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
  flex-flow: column;
}
.blog-navigation-latest-heading,
.blog-navigation-categories-heading {
  border-width: 0 0 1px 0;
  border-color: lightgray;
  border-style: solid;
}
.blog-navigation-latest-items,
.blog-navigation-categories-items {
  padding-top: 1em;
  overflow-x: hidden;
  overflow-y: auto;
}
.blog-navigation-latest-items .blog-link,
.blog-navigation-categories-items .blog-link {
  height: 48px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
  flex-flow: column;
  -ms-flex-pack: center;
  justify-content: center;
}
.blog-navigation-latest-items .blog-link:hover,
.blog-navigation-categories-items .blog-link:hover {
  cursor: pointer;
}
.blog-navigation-latest {
  grid-area: latest;
}
.blog-navigation-categories {
  grid-area: categories;
}
.blog-navigation#hide {
  width: 25vw;
}
.blog-navigation#show {
  width: 0vw;
}
.blog-hero {
  background-color: black;
  padding: 5vw;
  position: relative;
  grid-area: center;
  display: grid;
  -ms-flex-align: center;
  align-items: center;
  -webkit-transition: width 0.8s ease-in-out;
  -o-transition: width 0.8s ease-in-out;
  transition: width 0.8s ease-in-out;
}
.blog-hero-content {
  max-width: 35vw;
  z-index: 1;
  display: grid;
  grid-template-columns: 1fr;
  -ms-flex-align: end;
  align-items: flex-end;
}
.blog-hero-content h1 {
  font-family: "Rubik Mono One", sans-serif;
  font-size: 2.5rem;
  font-weight: bold;
}
.blog-hero-content * {
  color: white;
  margin: 2.5% 0;
}
.blog-hero#hide {
  width: 65vw;
}
.blog-hero#show {
  width: 25vw;
}
.blog-post {
  background-color: white;
  padding: 2em;
  position: relative;
  grid-area: content;
}
.blog-post-content {
  height: 100%;
  overflow-y: auto;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 2.5vw;
}
.blog-post-content-section {
  display: grid;
  grid-template-areas: "heading image" "text image";
  grid-template-rows: auto
          1fr;
  grid-template-columns: 1fr 1fr;
}
.blog-post-content-section-heading {
  border-width: 0 0 1px 0;
  border-color: lightgray;
  border-style: solid;
  grid-area: heading;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
  align-items: flex-end;
}
.blog-post-content-section-text {
  text-align: justify;
  padding: 5% 5% 5% 0;
  grid-area: text;
}
.blog-post-content-section-image {
  grid-area: image;
}
.blog-post-content-section-image img {
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.blog-post #posts-open {
  background-color: white;
  width: 152px;
  position: absolute;
  bottom: 48px;
  left: -152px;
}
.blog-post #posts-close {
  background-color: white;
  position: absolute;
  bottom: 48px;
  left: -48px;
}
@-webkit-keyframes fade {
  0% {
    -webkit-transform: translateY(10px);
    transform: translateY(10px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes fade {
  0% {
    -webkit-transform: translateY(10px);
    transform: translateY(10px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }
}



