.montyhall {
  padding: 2em;
  display: grid;
  grid-template-areas: "heading" "slider" "interactive";
  grid-template-rows: 1fr
    400px
    400px;
}
.montyhall h2 {
  white-space: pre;
}
.montyhall-heading {
  text-align: center;
  font-family: "Rubik Mono One", sans-serif;
  font-size: 2.5rem;
  border-width: 0 0 1px 0;
  border-color: lightgray;
  border-style: solid;
  grid-area: heading;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.montyhall #montyhall-slider {
  text-align: justify;
  border-width: 0 0 1px 0;
  border-color: lightgray;
  border-style: solid;
  height: 100%;
  grid-area: slider;
}
.montyhall #montyhall-slider .slider-content {
  padding: 5%;
}
.montyhall-interactive {
  grid-area: interactive;
  display: grid;
  grid-template-areas: "scoreboard" "doors" "instructions";
  grid-template-rows: 120px
      1fr
      120px;
}
.montyhall-interactive-scoreboard {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}
.montyhall-interactive-scoreboard-table {
  width: 90%;
  max-height: 90%;
}
.montyhall-interactive-doors {
  margin: 5%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 5%;
}
.montyhall-interactive-doors .door-btn {
  border-width: 1px;
  border-color: lightgray;
  border-style: solid;
  height: 90%;
  -ms-flex-pack: center;
  justify-content: center;
}
.montyhall-interactive-doors .door-btn#highlight {
  border-color: black;
}
.montyhall-interactive-doors .door-btn#hover {
  -webkit-transform: scale(1.05, 1.05);
  -ms-transform: scale(1.05, 1.05);
  transform: scale(1.05, 1.05);
}
.montyhall-interactive-doors .door-btn[disabled] {
  background-color: whitesmoke;
  -webkit-transform: scale(1, 1);
  -ms-transform: scale(1, 1);
  transform: scale(1, 1);
  cursor: auto;
}
.montyhall-interactive-instructions {
  text-align: center;
  display: grid;
  grid-template-rows: 1fr 1fr;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.montyhall-interactive-instructions h2 {
  font-family: "Rubik Mono One", sans-serif;
  font-weight: normal;
}
.montyhall-interactive-instructions #montyhall-stickswitch {
  margin: 2.5%;
}
.montyhall-interactive-instructions #montyhall-select {
  border-width: 1px;
  border-color: lightgray;
  border-style: solid;
}
@media screen and (min-width: 800px) {
  .montyhall {
    grid-template-areas: "heading heading" "slider interactive";
    grid-template-rows: 1fr
      400px;
    grid-template-columns: 1fr 1fr;
  }
  .montyhall #montyhall-slider {
    border-width: 0 1px 0 0;
  }
  .montyhall-interactive-scoreboard {
    border-width: 0 0 1px 0;
    border-color: lightgray;
    border-style: solid;
  }
}



